@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
.information-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  bottom: 0;
  right: 0;
  width: 320px;
  height: 180px;
  padding: 20px;
  margin: 20px;
  background: #fff;
  border-radius: 8px;
}

.information-container .node-property-name {
  color: #afafaf;
  font-weight: 500;
}

.information-container .node-property-value {
  font-size: 1rem;
}

.about-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  top: 0;
  right: 0;
  width: 90px;
  height: 35px;
  margin: 20px;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  color: #808080;
  overflow: hidden;
  transition: width 1s, height 1s;
}

.about-container.openned {
  height: 244px;
  width: 475px;
}

.about-container .about-button {
  display: flex;
  flex-direction: row;
  align-self: flex-end;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  width: 90px;
  height: 35px;
  border-radius: 8px;
  cursor: pointer;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.about-container .about-button:hover {
  background: hsla(0, 0%, 90%, 0.5);
}

.about-container .about-content {
  padding: 15px;
}

.about-container .about-content p {
  margin: 5px;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font: 400 14px Roboto, sans-serif;
  background: #f0f0f5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body {
  display: flex;
  flex: 1 1;
  min-height: 100%;
  max-width: 100%;
}

#root {
  display: flex;
  flex: 1 1;
}

